@import "~normalize.css/normalize.css";
@import "~swiper/css/swiper.css";
@font-face {
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Radical";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Radical.woff2") format("woff2"), url("../fonts/Radical.woff") format("woff");
}
:root {
  font-size: 0.69445vw;
}
@media (max-width: 1024px) and (orientation: portrait) {
  :root {
    font-size: 3.125vw;
  }
}
@media (max-width: 1024px) and (orientation: portrait) and (min-width: 768px) {
  :root {
    font-size: 2.5vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  :root {
    font-size: 1vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) and (max-width: 768px) {
  :root {
    font-size: 2vw;
  }
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 1.8rem;
  line-height: 1.1;
  font-weight: 600;
  color: #ffffff;
  background-color: #5f458c;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 2000px) {
  body {
    font-size: 18px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body {
    min-height: calc(var(--vh, 1vh) * 100);
    font-size: 1.4rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body.menu-opened {
    overflow: hidden;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  body.menu-opened {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

.svg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -100;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes fadeInUp {
  from {
    transform: translateY(120%);
    opacity: 1;
  }
  to {
    transform: translateY(20%);
    opacity: 1;
  }
}
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  to {
    opacity: 0;
  }
}
@keyframes stretch {
  from {
    width: 0;
  }
}
@keyframes zoomIn {
  to {
    transform: scale(1, 1);
  }
}
@keyframes rollInRight {
  from {
    transform: translateX(150%) rotate(0deg);
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  to {
    opacity: 1;
    transform: translateX(0%) rotate(-360deg);
  }
}
@keyframes rollInLeft {
  from {
    transform: translateX(-150%) rotate(0deg);
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  to {
    opacity: 1;
    transform: translateX(0%) rotate(360deg);
  }
}
.animation-text {
  max-height: 12rem;
  display: flex;
}
.animation-text .word {
  overflow: hidden;
  display: flex;
}
.animation-text .word .letter {
  opacity: 0;
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
}

.animation-screen {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-image: url("../img/scene.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene@2x.png");
  }
}
@media (orientation: portrait) {
  .animation-screen {
    background-image: url("../img/scene-mob.png");
  }
}
@media (orientation: portrait) and (min-resolution: 144dpi), (orientation: portrait) and (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene-mob@2x.png");
  }
}

.btn {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: relative;
  padding: 1.5rem 2.9rem;
  text-transform: uppercase;
  text-align: center;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 3rem;
  color: #5f458c;
  letter-spacing: -0.04em;
}
.btn:focus {
  outline: none;
}
@media (min-width: 2000px) {
  .btn {
    padding: 15px 29px;
    font-size: 24px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
.btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 3.5rem;
  z-index: -1;
}
.btn:hover {
  color: #a67ee5;
}
.btn:focus {
  color: #a67ee5;
  outline: none;
}
.btn:focus::before {
  width: 103%;
  height: 108.5%;
  border-color: #a67ee5;
}
.btn:active, .btn:active:focus {
  color: #ffffff;
}
.btn:active::before, .btn:active:focus::before {
  width: 100%;
  height: 100%;
  background-color: #a67ee5;
  border-color: #a67ee5;
}

.chat {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.7rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .chat {
    padding: 0 57px;
  }
}
@media (orientation: portrait) {
  .chat {
    padding: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat {
    padding: 0;
  }
}

.chat__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 5.9rem 0;
  width: 100%;
  max-height: calc(100vh - 33.9rem);
  max-width: 79.8rem;
  overflow-y: auto;
}
@media (min-width: 2000px) {
  .chat__body {
    padding: 0 59px 0;
    max-height: calc(100vh - 339px);
    max-width: 798px;
  }
}
@media (orientation: portrait) {
  .chat__body {
    padding: 0 0 0;
    max-height: calc(var(--vh, 1vh) * 100 - 22.4rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .chat__body {
    max-height: calc(var(--vh, 1vh) * 100 - 25rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__body {
    padding: 0;
    max-height: calc(var(--vh, 1vh) * 100 - 16rem);
  }
}

.chat__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-top: auto;
}

.chat__message {
  flex: none;
  display: flex;
  margin-bottom: 2rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
}
@media (min-width: 2000px) {
  .chat__message {
    margin-bottom: 20px;
    font-size: 24px;
  }
}
@media (orientation: portrait) {
  .chat__message {
    font-size: 1.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__message {
    font-size: 1.6rem;
  }
}
.chat__message p {
  margin: 0;
  padding: 1.6rem 5.7rem;
  min-width: 17rem;
  border-radius: 4rem;
}
@media (min-width: 2000px) {
  .chat__message p {
    padding: 16px 57px;
    min-width: 170px;
    border-radius: 40px;
  }
}
@media (orientation: portrait) {
  .chat__message p {
    padding: 1rem 2.5rem;
    min-width: 10rem;
    max-width: calc(100% - 5rem);
    border-radius: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__message p {
    padding: 1rem 2.5rem;
    min-width: 10rem;
    max-width: calc(100% - 5rem);
    border-radius: 2rem;
  }
}
.chat__message--outcoming {
  justify-content: flex-start;
}
.chat__message--outcoming p {
  width: 100%;
  color: #5f458c;
  background-color: #ffffff;
  word-break: break-word;
}
.chat__message--incoming {
  justify-content: flex-end;
  text-align: center;
}
.chat__message--incoming p {
  color: #ffffff;
  background-color: #a67ee5;
  word-break: break-word;
}

.chat__placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 17rem;
  height: 6rem;
  background-color: #a67ee5;
  border-radius: 3rem;
}
@media (min-width: 2000px) {
  .chat__placeholder {
    width: 170px;
    height: 60px;
    border-radius: 30px;
  }
}
@media (orientation: portrait) {
  .chat__placeholder {
    width: 10rem;
    height: 4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__placeholder {
    width: 10rem;
    height: 4rem;
  }
}
.chat__placeholder span {
  margin: 0 0.65rem;
  width: 1rem;
  height: 1rem;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (min-width: 2000px) {
  .chat__placeholder span {
    margin: 0 6.5px;
  }
}
@media (orientation: portrait) {
  .chat__placeholder span {
    margin: 0 0.5rem;
    width: 0.6rem;
    height: 0.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__placeholder span {
    margin: 0 0.5rem;
    width: 0.6rem;
    height: 0.6rem;
  }
}
.chat__placeholder--hidden {
  opacity: 0;
}

.chat__footer {
  margin-top: auto;
}

.disclaimer {
  color: #5f458c;
  background-color: #ffffff;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer {
    font-weight: 400;
    color: #ffffff;
    background-color: transparent;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer {
    font-weight: 400;
    color: #ffffff;
    background-color: transparent;
  }
}
.disclaimer ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 1.2rem;
  line-height: 1.2;
}
@media (min-width: 2000px) {
  .disclaimer ul {
    font-size: 12px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer ul {
    font-size: 1rem;
    opacity: 0.5;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer ul {
    font-size: 1rem;
    opacity: 0.5;
  }
}
.disclaimer li {
  position: relative;
  padding: 0 0.6rem;
}
@media (min-width: 2000px) {
  .disclaimer li {
    padding: 0 6px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
.disclaimer li::before {
  content: "/";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-50%, -50%);
}
.disclaimer li:first-child::before {
  display: none;
}
.disclaimer a {
  display: inline-block;
  vertical-align: top;
  padding: 0.9rem 0 0.7rem;
  text-decoration: underline;
  color: inherit;
}
.disclaimer a:hover {
  opacity: 0.8;
}
.disclaimer a:focus {
  outline: none;
  opacity: 0.7;
}
@media (min-width: 2000px) {
  .disclaimer a {
    padding: 9px 0 7px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer a {
    padding: 0.9rem 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer a {
    padding: 0.9rem 0;
  }
}

:root {
  --width-input-result: calc(100% - 31rem);
  --padding-input-result: 0 5.6rem;
}
@media (min-width: 2000px) {
  :root {
    --width-input-result: calc(100% - 310px);
    --padding-input-result: 0 56px;
  }
}
@media (orientation: portrait) {
  :root {
    --width-input-result: calc(100% - 5rem);
    --padding-input-result: 0 2.5rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  :root {
    --width-input-result: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  :root {
    --width-input-result: calc(100% - 5rem);
    --padding-input-result: 0 2.5rem;
  }
}

.form {
  position: relative;
  display: flex;
  align-items: center;
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1.2rem;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1.2;
  opacity: 0;
  pointer-events: none;
}
@media (min-width: 2000px) {
  .form__label {
    margin-bottom: 12px;
    font-size: 16px;
  }
}
@media (orientation: portrait) {
  .form__label {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field {
  flex: none;
  margin-right: 3rem;
  padding: 0;
  opacity: 0;
  height: 6rem;
  transition: padding 0.1s ease-out, width 0.3s ease-in-out, opacity 0s;
  width: 0;
  margin-left: auto;
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: #5f458c;
  border: 0;
  border-radius: 3rem;
  outline: none;
}
.form__field:focus {
  border: 1px solid #a67ee5;
}
@media (min-width: 2000px) {
  .form__field {
    margin-right: 30px;
    padding: 0 56px;
    height: 60px;
    font-size: 24px;
    border-radius: 30px;
  }
}
@media (orientation: portrait) {
  .form__field {
    margin-right: 1rem;
    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__field {
    margin-right: 1rem;
    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
  }
}

.form__button {
  flex-grow: 1;
  max-width: 235px;
}
@media (orientation: portrait) {
  .form__button {
    position: relative;
    flex: none;
    padding: 0;
    width: 4rem;
    height: 4rem;
    font-size: 0;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button {
    position: relative;
    flex: none;
    padding: 0;
    width: 6rem;
    height: 6rem;
    font-size: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__button {
    width: 4rem;
    height: 4rem;
  }
}
.form__button svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 1.6rem;
  height: 1.8rem;
}
@media (min-width: 2000px) {
  .form__button svg {
    width: 16px;
    height: 18px;
  }
}
@media (orientation: portrait) {
  .form__button svg {
    display: block;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button svg {
    display: block;
  }
}
.form__button.btn:before {
  max-width: 235px;
}

.form--result {
  display: flex;
  justify-content: flex-end;
}
.form--result .form__button {
  transition: opacity 0.2s ease-out 0.25s;
  box-sizing: content-box;
  display: inline-block;
  opacity: 0;
  max-width: unset;
}
.form--result .form__button span {
  transition: opacity 0.1s ease-out 0.35s;
  opacity: 0;
}
.form--result .form__button.btn:before {
  max-width: unset;
}
.form--result .form__field {
  transition: opacity 0.1s ease-out 0.35s, width 0.35s ease-out 0.4s;
  margin-left: auto;
  width: 0;
  opacity: 0;
}
.form--result .form__field::placeholder {
  opacity: 0;
  transition: opacity 0.2s ease-out 0.55s;
}

.screen.active .form__button {
  max-width: 235px;
  opacity: 1;
}
.screen.active .form__button span {
  opacity: 1;
}
.screen.active .form__field {
  opacity: 1;
  width: calc(100% - 31rem);
  padding: 0 5.6rem;
}
@media (min-width: 2000px) {
  .screen.active .form__field {
    padding: 0 56px;
    width: calc(100% - 210px);
  }
}
@media (orientation: portrait) {
  .screen.active .form__field {
    padding: 0 2.5rem;
    width: calc(100% - 5rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .screen.active .form__field {
    width: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .screen.active .form__field {
    padding: 0 2.5rem;
    width: calc(100% - 5rem);
  }
}
.screen.active .form__field::placeholder {
  opacity: 1;
}

@keyframes zoom {
  to {
    transform: scale(1) translateY(-30%);
  }
}
.svg-win {
  transform: scale(1.2) translateY(-25%);
  animation: zoom 0.5s ease-out forwards;
}

.game {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 14.6rem 0 6rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .game {
    padding: 146px 0 60px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game {
    padding: 5.9rem 0 4rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game {
    padding: 9rem 0 6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game {
    padding: 5rem 0 1.5rem;
  }
}

.game__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.1rem;
  padding-right: 0.6rem;
}
@media (min-width: 2000px) {
  .game__header {
    margin-bottom: 11px;
    padding-left: 6px;
  }
}
@media (orientation: portrait) {
  .game__header {
    margin-bottom: 1.5rem;
    padding-right: 0;
  }
}

.game__title {
  margin: 0;
  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;
}
@media (min-width: 2000px) {
  .game__title {
    font-size: 48px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__title {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game__title {
    font-size: 3rem;
    margin-bottom: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game__title {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}

.game__counter {
  text-align: left;
  margin: 0;
  display: flex;
  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: 0;
}
.game__counter span {
  width: 5rem;
}
.game__counter span:first-of-type {
  text-align: right;
}
@media (min-width: 2000px) {
  .game__counter {
    font-size: 48px;
  }
}
@media (min-width: 1024px) {
  .game__counter {
    margin-right: 5.6rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__counter {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game__counter {
    font-size: 3rem;
    margin-bottom: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game__counter {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}

.game__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.game__buttons {
  position: fixed;
  left: 0;
  z-index: 100;
  opacity: 0.5;
}

.game__button {
  margin: 0 30px 30px;
  display: block;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  padding: 15.5rem 0 10rem;
  width: 100%;
}
@media (min-width: 2000px) {
  .intro {
    padding: 155px 0 100px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro {
    padding: 6rem 0 5.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .intro {
    padding: 16vh 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .intro {
    padding: 16vh 0 5vh;
  }
}

.intro__title {
  margin: 0;
  max-width: 99rem;
  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12rem;
  line-height: 0.906;
  letter-spacing: -0.04em;
}
@media (min-width: 2000px) {
  .intro__title {
    max-width: 990px;
    font-size: 120px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro__title {
    max-width: 100%;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .intro__title {
    font-size: 10rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .intro__title {
    font-size: 5rem;
  }
}

.intro__content {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-left: 6.3rem;
}
@media (min-width: 2000px) {
  .intro__content {
    padding-left: 63px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro__content {
    justify-content: flex-start;
    padding-left: 2.5rem;
  }
}

.intro__info {
  flex: none;
  width: 50rem;
  max-width: 50%;
}
@media (min-width: 2000px) {
  .intro__info {
    width: 500px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro__info {
    display: none;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .intro__info {
    display: none;
  }
}

.intro__message {
  flex: none;
  width: 50.9rem;
  max-width: 50%;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.27;
}
@media (min-width: 2000px) {
  .intro__message {
    width: 509px;
    font-size: 24px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro__message {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .intro__message {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }
}
.intro__message p {
  margin: 0;
  transition: 0.5s cubic-bezier(0.15, 0.67, 0.56, 0.96) 0.4s;
  opacity: 0;
  transform: translateY(30%);
}
@media (min-width: 2000px) {
  .intro__message svg {
    width: 16px;
    height: 16px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro__message svg {
    width: 1.1rem;
    height: 1.1rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .intro__message svg {
    width: 1.1rem;
    height: 1.1rem;
  }
}

.screen.active .intro__message p {
  opacity: 1;
  transform: translateY(0%);
}

.intro__label {
  margin: 0 0 1.6rem;
  max-width: 25rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
}
@media (min-width: 2000px) {
