    padding: 0 1rem;
  }
}

.page-header__logo {
  transform: translateX(-30%);
  opacity: 0;
  transition: 0.35s cubic-bezier(0.32, 0.87, 0.35, 0.93);
  position: relative;
  flex: none;
  top: -0.5rem;
  display: block;
  padding: 1rem 0;
  width: 10rem;
  color: #ffffff;
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:focus {
  outline: none;
  opacity: 0.7;
}
@media (min-width: 2000px) {
  .page-header__logo {
    top: -5px;
    padding: 10px 0;
    width: 113px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .page-header__logo {
    top: 0;
    padding: 0.5rem 0;
    width: 6.8rem;
    z-index: 1;
  }
  .page-header--menu-opened .page-header__logo {
    color: #5f458c;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .page-header__logo {
    top: 0;
    padding: 0.5rem 0;
    width: 6.8rem;
    z-index: 1;
  }
  .page-header--menu-opened .page-header__logo {
    color: #5f458c;
  }
}
.page-header__logo svg {
  display: block;
  width: 100%;
  height: auto;
}

.page-header__toggler {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: relative;
  display: none;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 0;
  background-color: #ffffff;
  border-radius: 50%;
  z-index: 1;
  transform: scale(0);
}
.page-header__toggler:focus {
  outline: none;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .page-header__toggler {
    display: block;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .page-header__toggler {
    display: block;
  }
}
.page-header__toggler::before, .page-header__toggler::after {
  content: "";
  position: absolute;
  right: 0.8rem;
  height: 1px;
  background-color: #5f458c;
}
.page-header__toggler::before {
  top: 0.9rem;
  width: 1rem;
  box-shadow: 0 0.6rem 0 0 #5f458c;
}
.page-header__toggler::after {
  top: 1.2rem;
  width: 0.7rem;
}
.page-header--menu-opened .page-header__toggler::before, .page-header--menu-opened .page-header__toggler::after {
  top: 50%;
  left: 50%;
  transform: translate(-50%) rotate(45deg);
  width: 2.6rem;
  height: 2px;
  box-shadow: none;
}
.page-header--menu-opened .page-header__toggler::after {
  transform: translate(-50%) rotate(-45deg);
}

.page-header__nav {
  display: flex;
  align-items: center;
  opacity: 0;
  transition: 0.35s cubic-bezier(0.32, 0.87, 0.35, 0.93);
}
@media (max-width: 1024px) and (orientation: portrait) {
  .page-header__nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateX(-100%);
    flex-direction: column;
    align-items: flex-start;
    padding: 4rem 3.5rem;
    color: #5f458c;
    background-color: #ffffff;
  }
  .page-header--menu-opened .page-header__nav {
    transform: translateX(0);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .page-header__nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateX(-100%);
    flex-direction: column;
    align-items: flex-start;
    padding: 2rem 3.5rem;
    max-height: calc(var(--vh, 1vh) * 100);
    color: #5f458c;
    background-color: #ffffff;
    overflow-y: auto;
  }
  .page-header--menu-opened .page-header__nav {
    transform: translateX(0);
  }
}

