    font-size: 1.4rem;
    color: #5f458c;
  }
}
.page-header__menu ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .page-header__menu ul {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 3.8rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .page-header__menu ul {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 2rem;
  }
}
.page-header__menu li {
  margin-left: 4rem;
}
@media (min-width: 2000px) {
  .page-header__menu li {
    margin-left: 40px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .page-header__menu li {
    margin: 0 0 2.5rem;
  }
  .page-header__menu li:last-child {
    margin-bottom: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .page-header__menu li {
    margin: 0 0 1.5rem;
  }
  .page-header__menu li:last-child {
    margin-bottom: 0;
  }
}
.page-header__menu a {
  position: relative;
  display: block;
  color: inherit;
  padding: 0 0.5em 0.5em;
}
.page-header__menu a::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 0;
  height: 2px;
  background-color: #ffffff;
  right: 0;
  left: auto;
  transition-duration: 0.2s;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .page-header__menu a::after {
    left: 0;
    right: 0;
    background-color: #5f458c;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .page-header__menu a::after {
    left: 0;
    right: 0;
    background-color: #5f458c;
  }
}
.page-header__menu a.active {
  color: #fff !important;
}
.page-header__menu a.active::after {
  left: 0;
  transition-delay: 0.35s;
  transition-property: width;
  width: 100%;
  background-color: #fff !important;
}
.page-header__menu a:hover, .page-header__menu a:focus {
  color: #a67ee5;
  outline: none;
}
.page-header__menu a:hover::after, .page-header__menu a:focus::after {
  width: 100%;
  opacity: 1;
  background-color: #a67ee5;
  left: 0;
  right: auto;
}

